.img-fluid{
  max-width: 100%;
  height: auto;
}
.d-flex{
  display: flex;
}
.ant-layout{
  background: #f5f5f5;
}
h1,h1.ant-typography, .ant-typography h1{
  font-size: 3.75rem;
  line-height: 1;
  font-weight: bold;
}
h2,h2.ant-typography, .ant-typography h2{
  font-size: 3rem;
  line-height: 1;
  font-weight: bold;
}
div.ant-typography, .ant-typography p{
  font-size: 1.875rem;
}
.ant-btn-lg{
  font-size: 18px;
  height: 45px;
  border-radius: 4px;
  background: #fb8c00;
  font-weight: 600;
}
.text-center{
  text-align: center;
}